.imgPosition::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.shadeStyle::before {
  background: -moz-linear-gradient(90deg, #202125 0, rgba(32, 33, 37, .6) 50%, rgba(32, 33, 37, 0) 70%, rgba(32, 33, 37, 0) 80%, #202125 100%);
  background: -webkit-linear-gradient(90deg, #202125 0, rgba(32, 33, 37, .6) 50%, rgba(32, 33, 37, 0) 70%, rgba(32, 33, 37, 0) 80%, #202125 100%);
  background: linear-gradient(90deg, #202125 0, rgba(32, 33, 37, .6) 50%, rgba(32, 33, 37, 0) 70%, rgba(32, 33, 37, 0) 80%, #202125 100%);
}

.shadeText {
  position: absolute;
  bottom: 30px;
  left: 2%;
  right: 40%;
}


.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: .4
  }

  to {
    opacity: 1
  }
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -22px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  user-select: none;
  border-radius: 4px;
}

.prev {
  /* border-radius: 0 3px 3px 0; */
  left: 0;
}

.next {
  /* border-radius: 3px 0 0 3px; */
  right: 0;
}

.prev:hover,
.next:hover {
  /* background-color: rgba(0, 0, 0, 0.8); */
  background: #00000077;
}

.slideDot {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dot {
  cursor: pointer;
  height: 5px;
  width: 28px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 3px;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.dot:hover {
  background-color: #717171;
}